import React from "react";

function Jumbotron() {
  return (
    <div className="imageBox">
      <img
        src="https://t.ly/nqVs"
        className="img-fluid"
        width="100%"
        style={{ objectFit: "cover", objectPosition: "top" }}
      ></img>
    </div>
  );
}

export default Jumbotron;
